<!-- eslint-disable vue/no-unused-components -->
<!-- eslint-disable vue/require-v-for-key -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable max-len -->
<template>
    <main class="content container">
      <div class="content__top content__top--catalog">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          152 товара
        </span>
      </div>

      <div class="content__catalog">

      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color.sync="filterColor"/>

        <section class="catalog">

          <div v-if="productsLoading">Загрузка товаров...</div>
          <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товаров... <button @click.prevent="loadProducts">Попробовать еще раз!</button></div>

          <ProductList :products="products" @gotoPage="(pageName, pageParams) => $emit('gotoPage', pageName, pageParams)" :product-data = "productsData"/>

          <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" :paginate-prew.sync="page" :paginate-next.sync="page"/>

        </section>
      </div>
    </main>
  </template>
  <!-- eslint-disable linebreak-style -->
  <!-- eslint-disable no-return-assign -->
  <!-- eslint-disable import/no-extraneous-dependencies -->
<script>
// import products from '@/data/products';
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePaginstion.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '../config';

export default {
  components: {
    ProductList,
    BasePagination,
    ProductFilter,
  },
  data() {
    return {
      page: 1,
      productsPerPage: 3,

      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: 0,

      productsData: null,

      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  methods: {

    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColor,
            },
          })
          .then((response) => this.productsData = response.data)
          .catch(() => this.productsLoadingFailed = true)
          .then(() => this.productsLoading = false);
      }, 0);
    },
  },

  created() {
    this.loadProducts();
  },

  computed: {
    // filteredProducts() {
    //   let filteredProducts = products;

    //   if (this.filterPriceFrom > 0) {
    //     filteredProducts = filteredProducts
    //       .filter((product) => product.price > this.filterPriceFrom);
    //   }
    //   if (this.filterPriceTo > 0) {
    //     filteredProducts = filteredProducts
    //       .filter((product) => product.price < this.filterPriceTo);
    //   }
    //   if (this.filterCategoryId) {
    //     filteredProducts = filteredProducts
    //       .filter((product) => product.categoryId === this.filterCategoryId);
    //   }
    //   if (this.filterColor) {
    //     filteredProducts = filteredProducts
    //       .filter((product) => product.color === this.filterColor);
    //   }

    //   return filteredProducts;
    // },
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => ({
          ...product,
          image: product.image.file.url,
        }))
        : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },

  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColor() {
      this.loadProducts();
    },
  },
};
</script>
  <!-- eslint-disable linebreak-style -->
<style scoped lang="stylus">
  main
    height 100%
</style>
