<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
    <main class="content container">
        <div v-show="!$store.state.cartLoaded">Корзина загружается</div>
        <div v-show="$store.state.cartLoadedFailed">Ошибка загрузки корзины!</div>
        <div class="content__top">
            <ul class="breadcrumbs">
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link" href="index.html">
                        Каталог
                    </a>
                </li>
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link">
                        Корзина
                    </a>
                </li>
            </ul>

            <h1 class="content__title">
                Корзина
            </h1>
            <span class="content__info">
                {{ productInCart }} {{ wordEnding }}
            </span>
        </div>

        <section class="cart">
            <form class="cart__form form" action="#" method="POST">
                <div class="cart__field">
                    <ul class="cart__list">

                        <CartItem v-for="item in products" :key="item.id" :item="item"/>

                    </ul>
                </div>

                <div class="cart__block">
                    <p class="cart__desc">
                        Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
                    </p>
                    <p class="cart__price">
                        Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
                    </p>

                    <router-link tag="button" :to="{name: 'order'}" class="cart__button button button--primery" type="submit" :disabled="!cartProductAvailability">
                        Оформить заказ
                    </router-link>
                </div>
            </form>
        </section>
    </main>
</template>
<!-- eslint-disable linebreak-style -->
<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import CartItem from '@/components/CartItem.vue';

export default {
  data() {
    return {
      productCartAvailability: false,
    };
  },

  components: {
    CartItem,
  },
  methods: {
    // cartProductAvailability() {
    //   const cartProduct = this.$store.state.cartProductsData;
    //   if (cartProduct.length === 0) {
    //     this.productCartAvailability = true;
    //   }
    // },
  },

  computed: {
    ...mapGetters(
      {
        products: 'cartDetailsProducts',
        totalPrice: 'cartTotalPrice',
        productInCart: 'quantityProductInCart',
        wordEnding: 'wordEndingProduct',
      },
    ),

    // products() {
    //   return this.$store.getters.cartDetailsProducts;
    // },
    cartProductAvailability() {
      return this.$store.state.cartProductsData.length;
    },
  },

  filters: {
    numberFormat,
  },
};
</script>
