<!-- eslint-disable linebreak-style -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable vue/no-multiple-template-root -->
<!-- eslint-disable max-len -->
<template>
    <li class="cart__item product">
        <div class="product__pic">
            <img :src="item.product.image" width="120" height="120" srcset="img/phone-square-3@2x.jpg 2x"
                :alt="item.product.title">
        </div>
        <h3 class="product__title">
        {{ item.product.title }}
        </h3>
        <!-- <p class="product__info">
            Объем: <span>128GB</span>
        </p> -->
        <span class="product__code">
            Артикул: {{ item.product.id }}
        </span>

        <div class="product__counter form__counter">
            <button type="button" aria-label="Убрать один товар" @click="deleteOneProduct">
                <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                </svg>
            </button>

            <input type="text" name="count" v-model.number="amount">

            <button type="button" aria-label="Добавить один товар" @click="addOneProduct">
                <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                </svg>
            </button>
        </div>

        <b class="product__price">
            {{ (item.amount * item.product.price) | numberFormat }} ₽
        </b>

        <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click="deleteProduct(item.productId)">
            <svg width="20" height="20" fill="currentColor">
                <use xlink:href="#icon-close"></use>
            </svg>
        </button>
    </li>
</template>
<!-- eslint-disable linebreak-style -->
<script>
import numberFormat from '@/helpers/numberFormat';
import { mapMutations, mapActions } from 'vuex';

export default {
  props: ['item'],

  methods: {
    ...mapMutations({
      deleteProduct: 'deleteCartProduct',
    }),
    ...mapActions(['deleteProductToCart']),

    // deleteProduct(productId) {
    //   this.$store.commit('deleteCartProduct', productId);
    // },

    deleteProduct() {
      this.deleteProductToCart({ productId: this.item.product.id });
    },

    deleteOneProduct() {
      if (this.amount > 0) {
        this.amount -= 1;
      }
    },

    addOneProduct() {
      this.amount += 1;
    },
  },

  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value });
      },
    },
  },

  filters: {
    numberFormat,
  },
};
</script>
