<!-- eslint-disable vue/no-parsing-error -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable max-len -->
<template>
    <main class="content container">
        <div class="content__top">
            <ul class="breadcrumbs">
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link" href="index.html">
                        Каталог
                    </a>
                </li>
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link" href="cart.html">
                        Корзина
                    </a>
                </li>
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link">
                        Оформление заказа
                    </a>
                </li>
            </ul>

            <h1 class="content__title">
                Корзина
            </h1>
            <span class="content__info">
                3 товара
            </span>
            <div v-show="sendingOrder"> Ваш заказ отправляется для обработки...</div>
        </div>

        <section class="cart">
            <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
                <div class="cart__field">
                    <div class="cart__data">
                        <BaseFormTextField v-model="formData.name" :error="formError.name" title="ФИО" placeholder="Введите ваше полное имя"/>
                        <BaseFormTextField v-model="formData.address" :error="formError.address" title="Адрес доставки" placeholder="Введите ваш адрес"/>
                        <BaseFormTextField type="tel" v-model="formData.phone" :error="formError.phone" title="Телефон" placeholder="Введите ваш телефон"/>
                        <BaseFormTextField type="email" v-model="formData.email" :error="formError.email" title="ФИО" placeholder="Email"/>
                        <BaseFormTextareaField title="Комментарий к заказу" v-model="formData.comment" :error="formError.comment" placeholder="Ваши пожелания"/>
                    </div>

                    <div class="cart__options">
                        <h3 class="cart__title">Доставка</h3>
                        <ul class="cart__options options">
                            <li class="options__item">
                                <label class="options__label">
                                    <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                                    <span class="options__value">
                                        Самовывоз <b>бесплатно</b>
                                    </span>
                                </label>
                            </li>
                            <li class="options__item">
                                <label class="options__label">
                                    <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                                    <span class="options__value">
                                        Курьером <b>500 ₽</b>
                                    </span>
                                </label>
                            </li>
                        </ul>

                        <h3 class="cart__title">Оплата</h3>
                        <ul class="cart__options options">
                            <li class="options__item">
                                <label class="options__label">
                                    <input class="options__radio sr-only" type="radio" name="pay" value="card">
                                    <span class="options__value">
                                        Картой при получении
                                    </span>
                                </label>
                            </li>
                            <li class="options__item">
                                <label class="options__label">
                                    <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                                    <span class="options__value">
                                        Наличными при получении
                                    </span>
                                </label>
                            </li>
                        </ul>
                    </div>
                </div>

                <div class="cart__block">
                    <ul class="cart__orders">
                        <li class="cart__order" v-for="prooduct in products" :key="prooduct.product.id">
                            <h3>{{ prooduct.product.title }}</h3>
                            <b>{{ prooduct.product.price | numberFormat }}</b>
                            <span>Артикул: {{ prooduct.product.id }}</span>
                        </li>
                    </ul>

                    <div class="cart__total">
                        <p>Доставка: <b>500 ₽</b></p>
                        <p>Итого: <b>{{ productInCart }}</b> {{ wordEnding }} на сумму <b>{{ totalPrice | numberFormat }}</b></p>
                    </div>

                    <button class="cart__button button button--primery" type="submit" >
                        Оформить заказ
                    </button>
                </div>
                <div class="cart__error form__error-block" v-if="formErrorMessage">
                    <h4>Заявка не отправлена!</h4>
                    <p>
                        {{ formErrorMessage }}
                    </p>
                </div>
            </form>
        </section>
    </main>
</template>
<!-- eslint-disable linebreak-style -->
<!-- eslint-disable max-len -->
<script>
import BaseFormTextField from '@/components/BaseFormTextField.vue';
import BaseFormTextareaField from '@/components/BaseFormTextareaField.vue';
import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import { mapGetters } from 'vuex';
import { API_BASE_URL } from '../config';

export default {
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',

      sendingOrder: false,
    };
  },

  components: {
    BaseFormTextField,
    BaseFormTextareaField,
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';

      this.sendingOrder = true;

      axios.post(`${API_BASE_URL}/api/orders`, {
        ...this.formData,
      }, {
        params: {
          userAccessKey: this.$store.state.userAccessKey,
        },
      })
        .then((response) => {
          this.$store.commit('resetCart');
          this.sendingOrder = false;
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
          this.sendingOrder = false;
        });
    },
  },

  computed: {
    ...mapGetters(
      {
        products: 'cartDetailsProducts',
        totalPrice: 'cartTotalPrice',
        productInCart: 'quantityProductInCart',
        wordEnding: 'wordEndingProduct',
      },
    ),
    // cartProoducts() {
    //   return this.$store.state.cartProductsData;
    // },
    // cartPriceTotal() {
    //   return this.$store.state.cartProductsData.reduce((acc, product) => acc + product.product.price, 0);
    // },
  },

  filters: {
    numberFormat,
  },
};
</script>
